import './css/styles.css';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import refs from './references';
import { fetchPhoto } from './fetch-photo';

let photoCards = [];

// !---        !!!

//!? webformatURL -       .
//! largeImageURL -    .
//!? tags -    .    alt.
//!? likes -  .
//!? views -  .
//!? comments -  .
//!? downloads -  .

const getCardsMarkup = ({
  webformatURL,
  largeImageURL,
  tags,
  likes,
  views,
  comments,
  downloads,
}) => {
  return `
  <div class="photo-card">
    <a  href="${largeImageURL}">
      <img src="${webformatURL}" alt="${tags}" loading="lazy" />
    </a>    
    <div class="info">
      <p class="info-item"><b>Likes</b>${likes}</p>
      <p class="info-item"><b>Views</b>${views}</p>
      <p class="info-item"><b>Comments</b>${comments}</p>
      <p class="info-item"><b>Downloads</b>${downloads}</p>
    </div>
  </div>`;
};

const searchBtnDisableToggle = () => {
  refs.submitButton.toggleAttribute('disabled');
  refs.submitButton.classList.toggle('disabled');
  refs.iconSpinner.classList.toggle('is-hidden');
  refs.iconSearch.classList.toggle('is-hidden');
};

const render = () => {
  refs.gallery.insertAdjacentHTML(
    'beforeend',
    photoCards.map(getCardsMarkup).join('')
  );
};

// !--- 'Load More'

const onSearchFormSubmit = e => {
  e.preventDefault();
  const searchQuery = e.target.elements.searchQuery.value.trim();

  if (searchQuery === '') return;
  // ?searchButtonDisabled();

  searchBtnDisableToggle();

  // !--- HTML   
  // if (searchBoxValue === '') {
  //   clearHTML();
  //   return;
  // }

  fetchPhoto(searchQuery).then(({ data: { hits } }) => {
    if (hits.length === 0) {
      return Notify.failure(
        'Sorry, there are no images matching your search query. Please try again.'
      );
    }
    photoCards = hits;
    render();
    searchBtnDisableToggle();
    // const lightbox = new SimpleLightbox('.gallery a');
  });

  //   items = hits;
  //   console.log(items);

  //   searchBtnDisableToggle();
  // });

  // !--- 'Load More'   

  // fetchCountries(searchBoxValue)
  //   .then(data => {
  //     countries = data;
  //     render();
  //   })
  //   .catch(() => {
  //     return Notify.failure('Oops, there is no country with that name');
  //   });
};

const onGalleryImgClick = e => {
  e.preventDefault();
  if (e.target.nodeName !== 'IMG') return;
};

refs.searchForm.addEventListener('submit', onSearchFormSubmit);
refs.gallery.addEventListener('click', onGalleryImgClick);

